<template>
  <div class="row">
    <div class="col-md-5">
      <div class="box box-solid">
        <div class="box-header with-border">
          <i class="fa fa-user"></i>

          <h3 class="box-title"></h3>
        </div>
        <!-- /.box-header -->
        <div class="box-body">
          <table class="table no-border">
            <tbody>
            <tr>
              <td width="35%"></td>
              <td>{{order.address.name}}</td>
            </tr>
            <tr>
              <td></td>
              <td>{{order.address.phone}}</td>
            </tr>
            <tr>
              <td></td>
              <td>{{order.address.detail}}</td>
            </tr>
            <tr>
              <td></td>
              <td>{{order.residence}}</td>
            </tr>
            <tr>
              <td></td>
              <td>{{order.pay_at}}</td>
            </tr>
            </tbody>
          </table>
        </div>
        <!-- /.box-body -->
      </div>
      <div class="box box-solid">
        <div class="box-header with-border">
          <i class="fa fa-user-secret"></i>

          <h3 class="box-title"></h3>
        </div>
        <!-- /.box-header -->
        <div class="box-body">
          <table class="table no-border">
            <tbody>
            <tr>
              <td width="35%"></td>
              <td>{{order.staff.name}}</td>
            </tr>
            <tr>
              <td></td>
              <td>{{order.staff.phone}}</td>
            </tr>
            </tbody>
          </table>
        </div>
        <!-- /.box-body -->
      </div>


      <div class="box box-solid">
        <div class="box-header with-border">
          <i class="fa fa-building"></i>

          <h3 class="box-title"></h3>
        </div>
        <!-- /.box-header -->
        <div class="box-body">
          <table class="table no-border">
            <tbody>
            <tr>
              <td width="35%"></td>
              <td>{{order.staff.station.name}}</td>
            </tr>
            <tr>
              <td></td>
              <td>{{order.staff.station.address}}</td>
            </tr>
            <tr>
              <td></td>
              <td>{{order.staff.station.tel}}</td>
            </tr>
            <tr>
              <td></td>
              <td>{{order.staff.station.director}}</td>
            </tr>
            <tr>
              <td></td>
              <td>{{order.staff.station.phone}}</td>
            </tr>
            </tbody>
          </table>
        </div>
        <!-- /.box-body -->
      </div>
      <!-- /.box -->
    </div>
    <div class="col-md-7">
      <div class="box box-solid">
        <div class="box-header with-border">
          <i class="fa fa-list"></i>

          <h3 class="box-title"></h3>
        </div>
        <!-- /.box-header -->
        <div class="box-body">
          <table class="table no-border">
            <tbody>
            <tr>
              <td width="35%"></td>
              <td>{{order.order.order_no}}</td>
            </tr>
            <tr>
              <td width="35%"></td>
              <td>{{order.order.total_amount | currency ''}}</td>
            </tr>
            <tr>
              <td width="35%"></td>
              <td>
                {{order.order.discount_amount | currency ''}}
              </td>
            </tr>
            <tr v-if="order.order.promotions">
              <td width="35%"></td>
              <td v-for="promo in order.order.promotions">
                  <p>{{promo.promotion.name}}</p>
              </td>
            </tr>
            <tr>
              <td width="35%"></td>
              <td>{{order.order.pay_amount | currency ''}}</td>
            </tr>
            </tbody>
          </table>
        </div>
        <!-- /.box-body -->
      </div>
      <div class="box box-solid">
        <div class="box-header with-border">
          <i class="fa fa-gift"></i>

          <h3 class="box-title"></h3>
        </div>
        <!-- /.box-header -->
        <div class="box-body">
          <table class="table">
            <thead>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            </thead>
            <tbody>
            <tr>
              <td><img :src="order.sku.cover_image" alt="" width="30"></td>
              <td>{{order.sku.name}}</td>
              <td>{{order.sku.price | currency ''}}</td>
              <td>{{order.quantity}}</td>
            </tr>
            </tbody>
          </table>
        </div>
        <!-- /.box-body -->
      </div>
      <!-- /.box -->
    </div>
    <!-- ./col -->
  </div>
</template>
<script>
  import api from 'api/index.js'
  export default {
    name: 'ListStationCollectOrderDetail',
    data () {
      return {
        order: {},
        stations: []
      }
    },
    route: {
      data (transition) {
        return {
          order: api.stations.collect_orders.get(transition.to.params.order_no)
        }
      }
    },
    methods: {}
  }
</script>



